import React from "react";
import {
  HashRouter as Router,
  Switch,
  Route,
  useParams
} from "react-router-dom";
import Node from './node';

export default function App() {
  return (
    <Router>
        <Switch>
            <Route path="/:id" children={<Child />} />
        </Switch>
    </Router>
  );
}

function dbDriver(func) { // Array, ["title"]
    return function(db,dels){
        if (dels.length === 0) {
            return
        }

        let sect = db.findIndex(sect => ((sect["topic"] || sect["title"] || sect) === dels[0]))
        if (sect >= 0) {
            if (dels.length === 1) {
                func(db,sect)
            } else {
                dbDriver(func)(db[sect]["list"] || db[sect]["desc"],dels.slice(1))
            }
        }
    }
}

let delSubTitle = dbDriver(function(db,i){ db.splice(i,1) })
/* let addItem = function(item) { return dbDriver(function(db, i) {
    let size = db[i]["list"].length
    if (db[i]["list"][size-1]["title"] !== item["title"]) {
        db[i]["list"].push(item)
    }
}) } */

// del :: ["topic", "title", "title"...]
function genDB(dels) {
    let db = require('./db.json')
    dels.forEach(del => {
        delSubTitle(db, del)
    })
    return db
}

function Child() {
    let { id } = useParams();

    let dels = [], ilegal = false;
    if (id === "resume147") { // firmware
        dels = [
            ["Linux Administration"],
            ["Cloud service"],
            ["Reading", "Functional programming"],
            ["Reading", "System Design"],
            ["Reading", "Verification Automation"],
            ["Reading", "Rails"],
            ["Reading", "Green thread"]
        ]
    } else if (id === "resume258") { // web
        dels = [
            //["Dev Tools", "systemtap"],
            ["Dev Tools", "gdb"],
            //["Dev Tools", "bpf"],
            ["Dev Tools", "buildroot"],
            ["Reading", "Verification Automation"],
        ]
    } else if (id === "full582") {
        dels = []
    } else {
        ilegal = true;
    }

    let db
    if (!ilegal) {
        db = genDB(dels)
    } else {
        db = []
    }
    

    return (
    <div id="page-wrap">
        <div id="contact-info" className="vcard">
        
            <h1 className="fn">Chung-Feng Zheng</h1>
        
            <p>
                Cell: <span className="tel">0955-088-153</span><br />
                Email: <a className="email" href="mailto:zhengcf@cs.nctu.edu.tw">zhengcf@cs.nctu.edu.tw</a>
            </p>
        </div>
        <dl>
        {db.map(n => <Node now={n} />)}
    </dl>
    </div>
    );
}
